var timer = null
import {
	CARDPACKDETAILS
} from '../../../utils/api.js'
import uniLoadMore from '../../../components/uni-load-more/uni-load-more.vue'
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			cardId: '',
			cardDetails: '',
			infoList: [],
			page: 1,
			load: 'more',
			now: ''

		}
	},
	filters: {
		formateTel: function(value) {			
				var pat = /(\d{3})\d*(\d{4})/
				var b = value.replace(pat, '$1****$2');
				return b
		},
		formateCami: function(value) {
				var pat = /([a-zA-Z0-9]{4})[a-zA-Z0-9]*([a-zA-Z0-9]{4})/
				var b = value.replace(pat, '$1********$2');
				return b
		},
		formateDate: function(value) {
			var first = String(value).split(" ")[0]
			if (first !== undefined) {
				return first
			}
		},
		formateType(value) {
			var result
			if (value == 30) {
				result = '月卡'
			} else if (value == 90) {
				result = '季卡'
			} else if (value == 360) {
				result = '年卡'
			}
			return result
		}
	},
	onPullDownRefresh() {
		this.page = 1
		this.getDetail()
	},
	onReachBottom: function() {
		var _this = this
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_this.getmore();
		}, 1000);
	},
	onLoad(options) {
		uni.showToast({
			title:'加载中...',
			icon:'none',
			mask:true
		})
		this.cardId = options.id
		this.getDetail()
		this.getTime()
	},
	methods: {
		// 详情
		getDetail() {
			const params = {
				page: this.page,
				packId: this.cardId
			}
			this.$request.url_request(CARDPACKDETAILS, params, 'GET', res => {
				uni.hideToast()
				this.cardDetails = JSON.parse(res.data).data.card
				this.infoList = JSON.parse(res.data).data.details.records
				uni.stopPullDownRefresh()
			}, err => {})
		},
		//加载更多
		getmore() {
			if (this.load == 'more') {
				this.load == 'loading'
				this.$forceUpdate()
				this.page++
				const params = {
					page: this.page,
					packId: this.cardId
				}
				this.$request.url_request(CARDPACKDETAILS, params, 'GET', res => {
					if (JSON.parse(res.data).data.details.records.length <= 0) {
						this.load = 'moMore'
						this.$forceUpdate()
					} else {
						// this.cardDetails = JSON.parse(res.data).data.card
						this.infoList = this.infoList.concat(JSON.parse(res.data).data.details.records)
						this.load = 'more'
						this.$forceUpdate()
					}
					uni.stopPullDownRefresh()
				}, err => {})
			}
		},
		// 获取当前时间
	getTime() {
		var myDate = new Date();
		var year = myDate.getFullYear();
		var month = myDate.getMonth() + 1;
		var date = myDate.getDate();
		var h = myDate.getHours();
		var m = myDate.getMinutes();
		var s = myDate.getSeconds();
		this.now = year + '-' + this.conver(month) + "-" + this.conver(date) + " " + this.conver(h) + ':' + this.conver(m) +
			":" + this.conver(s);
		console.log(this.now)
	},
	conver(s) {
		return s < 10 ? '0' + s : s;
	},
		// 复制
		onCopyResult(type) {
			if (type === 'success') {
				uni.showToast({
					title: '复制成功',
					icon: 'none',
					duration: 1000
				})
			} else {}
		},
	}
}
